import { m } from "framer-motion";
// @mui
import { alpha, useTheme, styled } from "@mui/material/styles";
import { Box, Card, Container, Typography } from "@mui/material";
// components
import Image from "../../components/Image";
import { MotionViewport, varFade } from "../../components/animate";

// ----------------------------------------------------------------------

const CARDS = [
  {
    icon: "/static/icons/ic_ideas.svg",
    title: "Augmentez vos connaissances",
    description:
      "Plus vous en savez, plus il vous sera facile d'apprendre de nouvelles choses",
  },
  {
    icon: "/static/icons/ic_dollar.png",
    title: "Gagner un peu d'argent",
    description: "Plus vous répondez à des questions, plus vous jouez à n'importe quel jeu, plus vous gagnez d'argent.",
  },
  {
    icon: "/static/icons/ic_friendly.png",
    title: "Connexion conviviale",
    description:
      "Si vous cherchez simplement des jeux pour vous divertir, nous vous avons couvert.",
  },
];

const shadowIcon = (color) => `drop-shadow(2px 2px 2px ${alpha(color, 0.48)})`;

const RootStyle = styled("div")(({ theme }) => ({
  paddingTop: theme.spacing(15),
  [theme.breakpoints.up("md")]: {
    paddingBottom: theme.spacing(15),
  },
}));

const CardStyle = styled(Card)(({ theme }) => {
  const shadowCard = (opacity) =>
    theme.palette.mode === "light"
      ? alpha(theme.palette.grey[500], opacity)
      : alpha(theme.palette.common.black, opacity);

  return {
    border: 0,
    maxWidth: 380,
    minHeight: 340,
    margin: "auto",
    textAlign: "center",
    padding: theme.spacing(10, 5, 0),
    boxShadow: theme.customShadows.z12,
    [theme.breakpoints.up("md")]: {
      boxShadow: "none",
      // backgroundColor: theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
    },
    "&.cardLeft": {
      [theme.breakpoints.up("md")]: {},
    },
    "&.cardCenter": {
      [theme.breakpoints.up("md")]: {
        backgroundColor: theme.palette.background.paper,
        boxShadow: `-40px 40px 60px 0 ${shadowCard(0.2)}`,
        "&:before": {
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          zIndex: -1,
          content: "''",
          margin: "auto",
          position: "absolute",
          width: "calc(100% - 40px)",
          height: "calc(100% - 40px)",
          borderRadius: Number(theme.shape.borderRadius) * 2,
          backgroundColor: theme.palette.background.paper,
        },
      },
    },
    "&.cardRight": {
      [theme.breakpoints.up("md")]: {},
    },
  };
});

// ----------------------------------------------------------------------

export default function Homebling() {
  const theme = useTheme();

  const isLight = theme.palette.mode === "light";

  return (
    <RootStyle>
      <Container component={MotionViewport}>
        <Box
          sx={{
            textAlign: "center",
            mb: { xs: 5, md: 15 },
          }}
        >
          <m.div variants={varFade().inUp}>
            <Typography
              component="div"
              variant="overline"
              sx={{ mb: 2, color: "text.disabled" }}
            >
              bling
            </Typography>
          </m.div>
          <m.div variants={varFade().inDown}>
            <Typography variant="h2">Que vous propose bling ?</Typography>
          </m.div>
        </Box>

        <Box
          sx={{
            display: "grid",
            gap: { xs: 5, lg: 10 },
            gridTemplateColumns: { xs: "repeat(1, 1fr)", md: "repeat(3, 1fr)" },
          }}
        >
          {CARDS.map((card, index) => (
            <m.div variants={varFade().inUp} key={card.title}>
              <CardStyle
                className={
                  (index === 0 && "cardLeft") ||
                  (index === 1 && "cardCenter") ||
                  "cardRight"
                }
              >
                <Image
                  src={card.icon}
                  alt={card.title}
                  sx={{
                    mb: 6,
                    mx: "auto",
                    width: 40,
                    height: 40,
                    filter: (theme) => shadowIcon(theme.palette.primary.main),
                    ...(index === 0 && {
                      filter: (theme) => shadowIcon(theme.palette.info.main),
                    }),
                    ...(index === 1 && {
                      filter: (theme) => shadowIcon(theme.palette.error.main),
                    }),
                  }}
                />
                <Typography variant="h5" paragraph>
                  {card.title}
                </Typography>
                <Typography
                  sx={{ color: isLight ? "text.secondary" : "common.white" }}
                >
                  {card.description}
                </Typography>
              </CardStyle>
            </m.div>
          ))}
        </Box>
      </Container>
    </RootStyle>
  );
}
